import '@fontsource-variable/inter';
import { useHookstate } from '@hookstate/core';
import React, { useEffect } from 'react';
import { EVENTS, EVENT_SOURCES, EXPORT_STATES, LICENSE_ACTIVATION_STATES, PAGES } from '../../constants';
import store from '../../store';
import { ExportPage } from './ExportPage';
import { ManageLicensePage } from './ManageLicensePage';
import { HelpPage } from './HelpPage';
import './Popup.css';
import { Footer, Header } from './components/HeaderFooter';

const Popup = () => {
    const { currentPage, licenseKey, licenseActivationState, exportState } = useHookstate(store);

    chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
        if (request.destination === EVENT_SOURCES.POPUP) {
            if (request.eventType === EVENTS.DOWNLOAD_COMPLETE) {
                setTimeout(() => {
                    exportState.set(EXPORT_STATES.COMPLETED);
                }, 500);
            }
        }
    });

    useEffect(() => {
        chrome.storage.local.get('licenseStatus', function (res) {
            const key = res.licenseStatus?.key;
            if (key) {
                licenseKey.set(key);
                licenseActivationState.set(LICENSE_ACTIVATION_STATES.ACTIVATED);
            }
        });
    }, []);

    const SCREENS = {
        [PAGES.EXPORT]: <ExportPage />,
        [PAGES.LICENSE]: <ManageLicensePage />,
        [PAGES.HELP]: <HelpPage />,
    };

    return (
        <div className="App">
            <Header />
            <div className="max-width-container w-11/12 mx-auto">{SCREENS[currentPage.get()]}</div>
            <Footer />
        </div>
    );
};

export default Popup;
