/**
 * File contains utility functions
 */

import { CUSTOM_EVENT_NAME, EVENTS } from './constants';

/**
 * Function to deep copy an object. This will convert a JS proxy object to an actual object.
 * @param {*} obj
 */
export const deepCopyObject = (obj) => {
    return JSON.parse(JSON.stringify(obj));
};

export const maskString = (str, unmaskedChars) => {
    // Mask any non white space character
    return str.slice(0, unmaskedChars) + str.replaceAll(/[^\s\\]/g, '*');
};

export async function sendMessageAndWaitForReply({
    origin,
    destination,
    eventType,
    message,
}) {
    // Generate a unique ID for the message
    const messageId = Math.random().toString(36).substring(2);
    // Post the message to the target window
    window.dispatchEvent(
        new CustomEvent(CUSTOM_EVENT_NAME, {
            detail: {
                origin,
                destination,
                eventType,
                eventPayload: {
                    messageId,
                    message,
                },
            },
        }),
    );

    // Return a promise that resolves when the reply is received
    return new Promise((resolve, reject) => {
        // Create an event listener to handle replies
        function handleMessage(_event) {
            const event = { data: _event.detail };
            if (
                event.data &&
                event.data.eventType === EVENTS.REPLY &&
                event.data?.eventPayload?.messageId === messageId
            ) {
                // Remove the event listener
                window.removeEventListener('message', handleMessage);
                // Resolve the promise with the reply
                resolve(event.data.eventPayload.reply);
            }
        }

        window.addEventListener(CUSTOM_EVENT_NAME, handleMessage);
    });
}

export function sendMessageToContentScript(message) {
    return new Promise((resolve, reject) => {
        chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
            chrome.tabs.sendMessage(tabs[0].id, message, function (response) {
                resolve(response);
            });
        });
    });
}  