import React from 'react';
import classNames from 'classnames';

export const RadioButton = ({ id, hidden, checked = false, label, className, onClick }) => {
    return (
        <div className={classNames('flex items-center me-4', { hidden: hidden })}>
            <input
                id={id}
                type="checkbox"
                className={classNames(`w-4 h-4 accent-[#00A884] border-gray-300 rounded`, className)}
                defaultChecked={checked}
                onClick={onClick}
            />
            <label htmlFor={id} className="ms-2 text-sm font-medium text-gray-900 ml-1">
                {label}
            </label>
        </div>
    );
};
