import { useHookstate, useHookstateEffect } from '@hookstate/core';
import classNames from 'classnames';
import React, { useState, useEffect } from 'react';
import store from '../../../store';

const DropDownArrow = ({ pointDown }) => {
    return (
        <svg
            className={classNames('w-2.5 h-2.5 ms-3', { 'rotate-180': pointDown })}
            aria-hidden="true"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 10 6"
        >
            <path
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="m1 1 4 4 4-4"
            />
        </svg>
    );
};

export const DropDownWithSearchAndCheckbox = ({ items = [], label, hidden }) => {
    const { selectedItems, currentExportMode, extensionError } = useHookstate(store);
    const reorderedItems = items.sort((a, b) => {
        // Place the first element at the beginning
        if (a.id === 1) return -1;
        if (b.id === 1) return 1;

        // Place checked items next
        if (a.checked && !b.checked) return -1;
        if (!a.checked && b.checked) return 1;

        // Maintain the original order for unchecked items
        return 0;
    });

    const [filteredItems, setFilteredItems] = useState(reorderedItems);
    const [isOpen, setIsOpen] = useState(false);
    const [searchText, setSearchText] = useState('');
    const [_selectedItems, setselectedItems] = useState([]);

    useHookstateEffect(() => {
        console.log(items);
        setselectedItems([]);
        setFilteredItems(items);
        setSearchText('');
        setIsOpen(false);
    }, [currentExportMode]);

    useEffect(() => {
        const _filteredItems = items.filter(
            (item) => typeof item.name === 'string' && item.name.toLowerCase().includes(searchText.toLowerCase())
        );
        setFilteredItems(_filteredItems);
        selectedItems.set(_selectedItems);
    }, [searchText, items, _selectedItems]);

    const openCloseHandler = async () => {
        setIsOpen(!isOpen);
    };

    const onChangeHandler = (e) => {
        setSearchText(e.target.value);
    };

    const handlerCheckBoxClick = (e) => {
        console.log(e.target.id, e.target.value);
        const clickedItem = items.find((el) => el.id === e.target.id);
        clickedItem.checked = e.target.checked;
        const itemExistsInSelection = _selectedItems.find((el) => el.id === clickedItem.id);
        if (e.target.checked && !itemExistsInSelection) {
            setselectedItems([..._selectedItems, clickedItem]);
        } else if (!e.target.checked && itemExistsInSelection) {
            const newState = _selectedItems.filter((el) => el.id !== e.target.id);
            setselectedItems(newState);
        }
    };

    const getDropDownLabel = () => {
        if (!filteredItems.length || !_selectedItems.length) {
            return 'Select';
        }

        const label = _selectedItems.map((item) => item.name).join(', ');
        if (label.length > 30) return label.substring(0, 30) + '...';
        return label;
    };

    return (
        <div className={classNames('center', { hidden: hidden })}>
            <p className="mb-1">{label}</p>
            <button
                id="dropdownSearchButton"
                data-dropdown-toggle="dropdownSearch"
                data-dropdown-placement="bottom"
                className="text-gray-900 rounded-lg text-sm px-4 py-2 text-center inline-flex items-center border w-full justify-between"
                type="button"
                onClick={openCloseHandler}
            >
                {getDropDownLabel()}
                &nbsp;
                <DropDownArrow pointDown={isOpen} />
            </button>

            <div
                id="dropdownSearch"
                className={classNames('center z-10 bg-slate-50 rounded-lg shadow w-full', {
                    hidden: !isOpen,
                })}
            >
                <div className="p-3">
                    <label htmlFor="input-group-search" className="sr-only">
                        Search
                    </label>
                    <div className="relative">
                        <input
                            type="text"
                            id="input-group-search"
                            className="block w-full p-2 ps-10 text-sm text-gray-900 border border-gray-300 rounded-lg bg-gray-50"
                            placeholder="Search"
                            onChange={onChangeHandler}
                        />
                    </div>
                </div>
                <ul
                    className="h-48 px-3 pb-3 overflow-y-auto text-sm text-textBlack"
                    aria-labelledby="dropdownSearchButton"
                >
                    {filteredItems.map((item, index) => (
                        <li key={item.id}>
                            <div className="flex items-center ps-2 rounded hover:bg-slate-300 pl-1">
                                <input
                                    id={item.id}
                                    type="checkbox"
                                    value={item.name}
                                    className="w-4 h-4 accent-mainGreen bg-gray-100 border-gray-300 rounded"
                                    defaultChecked={item.checked}
                                    onClick={handlerCheckBoxClick}
                                />
                                <label
                                    htmlFor={item.id}
                                    className="w-full py-2 px-2 ms-2 text-sm text-textBlack font-medium rounded text-left"
                                >
                                    {item.name} {item.count !== undefined ? `(${item.count})` : null}
                                </label>
                            </div>
                        </li>
                    ))}
                </ul>
            </div>
            <div
                className={classNames('mt-2 text-yellow-600 text-center', {
                    hidden: items.length && extensionError.get() === null,
                })}
            >
                <p>
                    If you encounter an empty dropdown, please reopen the extension once WhatsApp has completely loaded
                    or refresh the page. If the issue persists, consider disabling other Chrome extensions.
                </p>
            </div>
        </div>
    );
};
