import { useHookstate, useHookstateEffect } from '@hookstate/core';
import classNames from 'classnames'
import React, { useEffect, useState } from 'react'
import {
  EVENTS,
  EVENT_SOURCES,
  EXPORT_MODES,
  EXPORT_STATES,
} from '../../constants'
import store from '../../store';
import { sendMessageToContentScript } from '../../utils'
import { DropDownWithSearchAndCheckbox } from './components/DropDownWithSearchAndCheckBox'
import LottieConnectingToWhatsAppAnimation from './components/LottieSpinnerAnimation'
import { RadioButton } from './components/RadioButton'
import { ButtonGroup } from './components/buttonGroup'
import { spinnerIcon } from './icons/spinner'
import { faBriefcase } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const exportIcon = (
  <svg
    height='13'
    viewBox='0 0 21 19'
    fill='none'
    xmlns='http://www.w3.org/2000/svg'
  >
    <path
      d='M1.61108 14.1827L2.16308 16.3916C2.25922 16.7762 2.48115 17.1176 2.7936 17.3616C3.10605 17.6056 3.49109 17.7382 3.88753 17.7382H17.1124C17.5089 17.7382 17.8939 17.6056 18.2063 17.3616C18.5188 17.1176 18.7407 16.7762 18.8369 16.3916L19.3889 14.1827M10.5 12.4049V1.73822V12.4049ZM10.5 12.4049L6.94442 8.84933L10.5 12.4049ZM10.5 12.4049L14.0555 8.84933L10.5 12.4049Z'
      stroke='white'
      strokeWidth='1.5'
      strokeLinecap='round'
      strokeLinejoin='round'
    />
  </svg>
);

export const ExportPage = () => {
  const _filters = [
    {
      id: 'exportOnlyUnsaved',
      label: 'Export only unsaved',
      type: 'boolean',
      checked: false,
    },
    {
      id: 'exportAllGroups',
      label: 'Export all groups',
      type: 'boolean',
      checked: false,
      displayConditionallyForExportModes: [EXPORT_MODES.GROUPS],
    },
  ];

  const [allGroups, setAllGroups] = useState([]);
  const [allLabels, setAllLabels] = useState([]);
  const [exportErrorHelpText, setExportErrorHelpText] = useState(null);
  const {
    currentExportMode,
    selectedItems,
    customerInfo,
    extensionError,
    exportState,
  } = useHookstate(store);
  const [_currentExportMode, setCurrentExportMode] = useState(
    currentExportMode.get(),
  );
  const [filters, setFilters] = useState(_filters);

  useHookstateEffect(() => {
    setCurrentExportMode(_currentExportMode);
  }, [currentExportMode]);

  useEffect(() => {
    let fetchInIntervalsUntilFound = setInterval(() => {
      if (!customerInfo.get().me) fetchRequiredData();
    }, 500);

    const fetchRequiredData = async () => {
      try {
        const reply = await sendMessageToContentScript({
          origin: EVENT_SOURCES.POPUP,
          destination: EVENT_SOURCES.DOM,
          eventType: EVENTS.FETCH_REQUIRED_DATA,
          eventPayload: null,
        });
        setAllGroups(reply.response.groups);
        setAllLabels(reply.response.labels);
        customerInfo.set({
          isBusinessUser: reply.response.isBusinessUser,
          me: reply.response.me,
        });
        if (reply.response.me) {
          clearInterval(fetchInIntervalsUntilFound);
        }
      } catch (error) {
        extensionError.set(1);
      }
    };
    fetchRequiredData();
  }, []);

  const getDropDownLabel = () => {
    if (currentExportMode.get() === EXPORT_MODES.GROUPS) return 'Select Groups';
    if (currentExportMode.get() === EXPORT_MODES.LABELS) return 'Select Labels';
  };

  const getDropDownItems = () => {
    if (currentExportMode.get() === EXPORT_MODES.GROUPS) {
      return allGroups;
    } else if (currentExportMode.get() === EXPORT_MODES.LABELS) {
      return allLabels;
    } else {
      return [];
    }
  };

  const validateBeforeExport = () => {
    if (currentExportMode.get() === EXPORT_MODES.GROUPS) {
      if (filters.find((f) => f.id === 'exportAllGroups').checked) return true;
      if (!selectedItems.get().length) return false;
    } else if (currentExportMode.get() === EXPORT_MODES.LABELS) {
      if (!selectedItems.get().length) return false;
    }
    return true;
  };

  const exportContacts = async (e) => {
    const exportMode = currentExportMode.get();
    const exportConfig = {
      exportMode,
      selectedItems: selectedItems.get(),
      filters,
    };
    if (!validateBeforeExport()) {
      const alertMessage = `Please select 1 or more ${currentExportMode.get()} to export.`;
      alert(alertMessage);
      return;
    }
    exportState.set(EXPORT_STATES.PROCESSING);
    // Show help text if download not initiated in 5 seconds
    setTimeout(() => {
      setExportErrorHelpText(
        `Some downloads may take a few seconds to complete. Please be patient and wait a little longer. If the download still doesn't complete, please try reloading the page. If the issue persists, feel free to reach out to support at codegenasite@gmail.com for further assistance."`,
      );
    }, 5000);
    sendMessageToContentScript({
      origin: EVENT_SOURCES.POPUP,
      destination: EVENT_SOURCES.CONTENT_SCRIPT,
      eventType: EVENTS.EXPORT_CONTACTS,
      eventPayload: exportConfig,
    });
  };

  const filterCheckBoxClickHandler = (e) => {
    const filterId = e.target.id;
    const filter = filters.find((f) => f.id === filterId);
    if (filter.checked && e.target.checked) {
      // Do nothing
      return;
    } else {
      const updatedFilters = filters;
      updatedFilters.find((f) => f.id === filterId).checked = e.target.checked;
      setFilters(updatedFilters);
    }
  };

  const getLabelExportErrorMessage = (customerInfo) => {
    const LABEL_EXPORT_ERROR_MESSAGES = {
      NOT_BUSINESS_ACCOUNT:
        'Label export is only available for business accounts.',
      FETCH_FAILED:
        'If you encounter an empty dropdown, please reopen the extension once WhatsApp has completely loaded or refresh the page. If the issue persists, consider disabling other Chrome extensions.',
    };
    if (!customerInfo.get().me) {
      return LABEL_EXPORT_ERROR_MESSAGES.FETCH_FAILED;
    } else if (!customerInfo.get().isBusinessUser) {
      return LABEL_EXPORT_ERROR_MESSAGES.NOT_BUSINESS_ACCOUNT;
    }
  };

  const extensionHasLoaded = () => {
    const hasLoaded = Boolean(customerInfo.get()?.me);
    return hasLoaded;
  };

  return (
    <>
      <ButtonGroup />
      <LottieConnectingToWhatsAppAnimation
        className={classNames({ hidden: extensionHasLoaded() })}
      />
      <div className={classNames({ hidden: !extensionHasLoaded() })}>
        <div
          className={classNames('flex text-left items-center justify-center', {
            hidden:
              currentExportMode.get() !== EXPORT_MODES.LABELS ||
              customerInfo.get().isBusinessUser,
          })}
        >
          <FontAwesomeIcon
            icon={faBriefcase}
            className='text-4xl text-gray-600 bg-gray-200 p-4 rounded-full'
          />
          <p className='text-sm pt-2 pl-4'>
            {getLabelExportErrorMessage(customerInfo)}
          </p>
        </div>
        <div
          className={classNames('export-ui', {
            hidden:
              currentExportMode.get() === EXPORT_MODES.LABELS &&
              !customerInfo.get().isBusinessUser,
          })}
        >
          <DropDownWithSearchAndCheckbox
            items={getDropDownItems()}
            label={getDropDownLabel()}
            hidden={currentExportMode.get() === EXPORT_MODES.CHATLIST}
          />

          <p
            className={classNames('text-center', {
              hidden: currentExportMode.get() !== EXPORT_MODES.CHATLIST,
            })}
          >
            Chatlist export may take a while for very large chat lists. Chats
            that are not viewable in the web may not be exported.
          </p>

          <div className='flex mt-4 select-none gap-2'>
            {filters.map((filter) => (
              <RadioButton
                id={filter.id}
                label={filter.label}
                checked={filter.checked}
                onClick={filterCheckBoxClickHandler}
                hidden={
                  filter.displayConditionallyForExportModes !== undefined &&
                  !filter.displayConditionallyForExportModes.includes(
                    currentExportMode.get(),
                  )
                }
              />
            ))}
          </div>

          <button
            className='bg-mainGreen hover:opacity-90 w-full rounded-md text-white mt-4 py-2 flex justify-center items-center'
            onClick={exportContacts}
          >
            {exportState.get() !== EXPORT_STATES.PROCESSING
              ? exportIcon
              : spinnerIcon}
            <span className='ml-2'> Export Contacts</span>
          </button>
          <p
            className={classNames('text-yellow-600 text-center mt-2', {
              hidden: exportState.get() !== EXPORT_STATES.PROCESSING,
            })}
          >
            {exportErrorHelpText}
          </p>
        </div>
      </div>
    </>
  );
};
