import { faMessage, faTag, faUserGroup } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useHookstate } from '@hookstate/core';
import classNames from 'classnames';
import React from 'react';
import store from '../../../store';
import { EXPORT_MODES, PAGES } from '../../../constants';

export const ButtonGroup = () => {
    const buttons = [
        {
            label: 'Groups',
            faIcon: faUserGroup,
            id: EXPORT_MODES.GROUPS,
        },
        {
            label: 'Chatlist',
            faIcon: faMessage,
            id: EXPORT_MODES.CHATLIST,
        },
        {
            label: 'Labels',
            faIcon: faTag,
            id: EXPORT_MODES.LABELS,
        },
    ];
    const { currentExportMode } = useHookstate(store);

    const handleButtonClick = (buttonId) => {
        if (buttonId !== currentExportMode.get()) {
            currentExportMode.set(buttonId);
        }
    };

    const isSelectedButton = (buttonId) => {
        return buttonId === currentExportMode.get();
    };

    return (
        <div className="inline-flex justify-center border-[#E6EDF5] w-full mt-4 mb-6 text-textBlack" role="group">
            {buttons.map((button, index) => (
                <button
                    type="button"
                    key={button.id}
                    className={classNames(
                        'inline-flex flex-grow items-center px-4 py-2 text-sm font-medium space-x-2',
                        {
                            'bg-mainGreen': isSelectedButton(button.id),
                            'bg-lightGray': !isSelectedButton(button.id),
                            'text-white': isSelectedButton(button.id),
                            'rounded-r-lg': index === buttons.length - 1,
                            'rounded-l-lg': index === 0,
                        }
                    )}
                    onClick={() => handleButtonClick(button.id)}
                >
                    <FontAwesomeIcon icon={button.faIcon} className="mr-2" />
                    {button.label}
                </button>
            ))}
        </div>
    );
};

export const BackButton = ({ previousPage = PAGES.EXPORT }) => {
    const { currentPage } = useHookstate(store);
    return (
        <button
            type="button"
            class="px-2 mb-2 flex items-center justify-center text-sm text-gray-700  border rounded-lg gap-x-2 sm:w-auto hover:bg-gray-100"
            onClick={() => {
                currentPage.set(previousPage);
            }}
        >
            <svg
                class="w-5 h-5 rtl:rotate-180"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke-width="1.5"
                stroke="currentColor"
            >
                <path stroke-linecap="round" stroke-linejoin="round" d="M6.75 15.75L3 12m0 0l3.75-3.75M3 12h18" />
            </svg>
            <span>Go back</span>
        </button>
    );
};