import {
    faCircleInfo,
    faEyeSlash,
    faKey,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useHookstate } from '@hookstate/core';
import classNames from 'classnames';
import React, { useState } from 'react';
import { EVENTS, EVENT_SOURCES, PAGES } from '../../../constants';
import manifest from '../../../manifest.json';
import store from '../../../store';
import { sendMessageToContentScript } from '../../../utils';
import { Logo } from './Logo';

const TrialBadge = (
    <div className='bg-[#FFE5B3] text-white py-1 px-2 rounded select-none'>
        <span className='text-xxs uppercase text-[#E27E1E] font-bold'>
            Trial
        </span>
    </div>
);

const LicensedBadge = (
    <div className='bg-mainGreen text-white py-1 px-2 rounded select-none'>
        <span className='text-xxs uppercase text-white font-bold'>
            Licensed
        </span>
    </div>
);

export const Header = () => {
    const { currentPage, licenseKey } = useHookstate(store);
    const [hideChats, setHideChats] = useState(false);

    const announcement = (
        <span>
            <a
                href='https://codegena.com'
                className='inline font-medium text-mainGreen underline underline-offset-2 decoration-600 dark:decoration-500 decoration-solid hover:no-underline'
            >
                Learn more&nbsp;
            </a>
            about the redesign
        </span>
    );
    const pageSwitcherHandler = (e) => {
        const clickedPageButtonId = e.currentTarget.id;
        console.log(currentPage.get(), clickedPageButtonId);
        if (currentPage.get() === clickedPageButtonId) {
            currentPage.set(PAGES.EXPORT);
        } else {
            currentPage.set(clickedPageButtonId);
        }
    };

    const hideChatsHandler = async () => {
        setHideChats(!hideChats);
        await sendMessageToContentScript({
            origin: EVENT_SOURCES.POPUP,
            destination: EVENT_SOURCES.DOM,
            eventType: EVENTS.TOGGLE_BLUR_CHATS,
            eventPayload: null,
        });
    };

    const BUTTON_SET = [
        {
            id: 'BLUR_CHAT',
            icon: faEyeSlash,
            title: 'Hide Chats',
            handler: hideChatsHandler,
        },
        {
            id: PAGES.HELP,
            icon: faCircleInfo,
            title: 'Help',
            handler: pageSwitcherHandler,
        },
        {
            id: PAGES.LICENSE,
            icon: faKey,
            title: 'Manage License',
            handler: pageSwitcherHandler,
        },
    ];

    return (
        <>
            <div className='bg-[#F1F2F6] p-4'>
                <div className='flex items-center justify-between'>
                    <div className='flex items-center space-x-2'>
                        <Logo />
                    </div>
                    <div className='inline-flex items-center gap-2 text-gray-600'>
                        {BUTTON_SET.map((button) => (
                            <FontAwesomeIcon
                                icon={button.icon}
                                id={button.id}
                                key={button.id}
                                title={button.title}
                                onClick={button.handler}
                                className={classNames(
                                    'text-lg hover:opacity-80 cursor-pointer',
                                    {
                                        'text-mainGreen':
                                            button.id === 'BLUR_CHAT'
                                                ? hideChats
                                                : currentPage.get() ===
                                                  button.id,
                                    },
                                )}
                            />
                        ))}
                        {licenseKey.get() ? LicensedBadge : TrialBadge}
                    </div>
                </div>
                <div className='version h-0 mb-1 text-[#0C7A63] font-bold'>
                    v{manifest.version}
                </div>
            </div>
            {/* <AnnouncementBanner message={announcement} /> */}
        </>
    );
};

export const Footer = () => {
    const { licenseKey } = useHookstate(store);
    const links = [
        {
            link: 'https://waxp-extension.notion.site/Product-Manual-934b9ab786bb433b8ca9970819246da0',
            label: 'Product Manual',
        },
        {
            link: 'mailto:codegenasite@gmail.com?subject=WAXP%20Lite%20Support',
            label: 'Contact Us',
        },
        {
            link: 'https://www.notion.so/waxp-extension/Changelog-c74fb24e91264ac58cba2d8f3ab1e3ca?pvs=4',
            label: 'Changelog',
        },
    ];

    return (
        <div className='max-width-container w-11/12 mx-auto'>
            <p className='text-gray-500 w-100 m-4 mb-2 text-center'>
                Disclaimer: WAXP is not affiliated to Whatsapp or Meta. Your
                data never leaves this browser.
            </p>
            <div className='links text-center flex gap-4 justify-center mb-4 text-mainGreen font-semibold'>
                <a
                    href='https://shaneapenkoshy.gumroad.com/l/WhatsApp-Contacts-Exporter'
                    className={classNames(
                        'hover:underline hover:underline-offset-2',
                        { hidden: licenseKey.get() },
                    )}
                    target='_blank'
                    rel='noreferrer noopener'
                >
                    Buy Now
                </a>
                {links.map((item) => (
                    <a
                        href={item.link}
                        target='_blank'
                        rel='noreferrer noopener'
                        className='hover:underline hover:underline-offset-2'
                    >
                        {item.label}
                    </a>
                ))}
            </div>
        </div>
    );
};
