import React from 'react'
import Lottie from 'react-lottie'
import * as animationData from './loadingAnimation.json'
import classNames from 'classnames'

const LottieConnectingToWhatsAppAnimation = ({
  isStopped,
  isPaused,
  className,
}) => {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  }

  return (
    <div className={classNames(className, 'text-center')}>
      <div className='pointer-events-none'>
        <Lottie
          options={defaultOptions}
          height={70}
          width={70}
          isStopped={isStopped}
          isPaused={isPaused}
        />
      </div>
      <p>Please wait while we connect to WhatsApp.</p>
      <p>
        Stuck in loading? See{' '}
        <a
          href='https://www.notion.so/waxp-extension/Troubleshooting-Guides-77e5526782cf47e4ab50341bb53e26f6?pvs=4'
          target='_blank'
          rel='noopener noreferrer'
          className='bg-gray-100 hover:bg-gray-200 text-gray-800 px-1.5 py-0.5 cursor-pointer'
        >
          troubleshooting guide
        </a>
      </p>
    </div>
  );
}

export default LottieConnectingToWhatsAppAnimation
