export const ManageLicensePage = ({}) => {
    const { licenseKey } = useHookstate(store);
    const [licenseActivationState, setLicenseActivationState] = useState(LICENSE_ACTIVATION_STATES.PENDING);

    useEffect(() => {
        chrome.storage.local.get('licenseStatus', function (res) {
            const key = res?.licenseStatus?.key;
            if (key) {
                licenseKey.set(key);
                setLicenseActivationState(LICENSE_ACTIVATION_STATES.ACTIVATED);
            }
        });
    }, []);
    const activateLicense = (key) => {
        setLicenseActivationState(LICENSE_ACTIVATION_STATES.PROCESSING);
        fetch(GUMROAD_LICENSE_ACTIVATION_URL, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                product_permalink: GUMROAD_PRODUCT_CODE,
                license_key: key,
                increment_uses_count: false,
            }),
        })
            .then((res) => res.json())
            .then(function (data) {
                if (data.success && data.purchase.refunded === false && data.purchase.chargebacked === false) {
                    chrome.storage.local.get('licenseStatus', function (res) {
                        const licenseDetails = {
                            key: data.purchase.license_key,
                            TRIAL_USAGE_COUNT: res?.licenseStatus?.TRIAL_USAGE_COUNT || 0,
                        };
                        chrome.storage.local.set({ licenseStatus: licenseDetails });
                    });
                    setLicenseActivationState(LICENSE_ACTIVATION_STATES.ACTIVATED);
                    licenseKey.set(data.purchase.license_key);
                    return true;
                } else {
                    setTimeout(() => {
                        setLicenseActivationState(LICENSE_ACTIVATION_STATES.FAILED);
                    }, 1500);
                    setTimeout(() => {
                        setLicenseActivationState(LICENSE_ACTIVATION_STATES.PENDING);
                    }, 3000);
                    return false;
                }
            })
            .catch(function (err) {
                alert('Some error occurred. Try again.');
                console.log(`An error occurred while fetching WAXP license key ${err}`);
            });
    };

    const deActivateLicense = () => {
        setLicenseActivationState(LICENSE_ACTIVATION_STATES.PROCESSING);
        setTimeout(() => {
            chrome.storage.local.remove('licenseStatus');
            licenseKey.set(null);
            setLicenseActivationState(LICENSE_ACTIVATION_STATES.PENDING);
        }, 1500);
    };

    const onFormSubmit = (e) => {
        e.preventDefault();
        if (licenseActivationState === LICENSE_ACTIVATION_STATES.ACTIVATED) {
            // Deactivate license
            deActivateLicense();
        } else {
            activateLicense(e.target.elements.licenseKey.value);
        }
    };

    const getLicenseButtonLabel = () => {
        const state = licenseActivationState;
        const BUTTON_LABEL_MAPPING = {
            [LICENSE_ACTIVATION_STATES.ACTIVATED]: 'Deactivate',
            [LICENSE_ACTIVATION_STATES.PENDING]: 'Activate',
            [LICENSE_ACTIVATION_STATES.PROCESSING]: 'Please wait..',
            [LICENSE_ACTIVATION_STATES.FAILED]: 'Failed',
        };
        return BUTTON_LABEL_MAPPING[state] || 'Invalid State';
    };

    const getButtonColorClass = () => {
        const COLOR = {
            GREEN: 'bg-mainGreen',
            GREY: 'bg-gray-700',
            RED: 'bg-red-500',
        };
        const state = licenseActivationState;
        if (state === LICENSE_ACTIVATION_STATES.PENDING) return COLOR.GREEN;
        if (state === LICENSE_ACTIVATION_STATES.ACTIVATED) return COLOR.GREY;
        if (state === LICENSE_ACTIVATION_STATES.FAILED) return COLOR.RED;

        if (state === LICENSE_ACTIVATION_STATES.PROCESSING && licenseKey.get()) return COLOR.GREY;
        if (state === LICENSE_ACTIVATION_STATES.PROCESSING && !licenseKey.get()) return COLOR.GREEN;
    };

    const getHeader = () => {
        const state = licenseActivationState;
        const LABEL_MAPPING = {
            [LICENSE_ACTIVATION_STATES.ACTIVATED]: 'Your license is activated',
            [LICENSE_ACTIVATION_STATES.PENDING]: 'Activate your license',
            [LICENSE_ACTIVATION_STATES.PROCESSING]: 'Processing..',
            [LICENSE_ACTIVATION_STATES.FAILED]: 'Activation Failed',
        };
        return LABEL_MAPPING[state] || 'Invalid State';
    };

    return (
        <div id="page3Content" className="mt-4">
            <BackButton />
            <div className="w-full">
                <form className="space-y-6" onSubmit={onFormSubmit}>
                    <h5 className="text-xl font-medium text-gray-900 mt-4">{getHeader()}</h5>
                    <div>
                        <label htmlFor="password" className="block mb-2 text-sm font-medium text-gray-900">
                            License key
                        </label>
                        <input
                            type="text"
                            name="licenseKey"
                            id="licenseKey"
                            placeholder={
                                licenseActivationState !== LICENSE_ACTIVATION_STATES.ACTIVATED ? '••••••••' : ''
                            }
                            defaultValue={
                                licenseActivationState === LICENSE_ACTIVATION_STATES.ACTIVATED
                                    ? licenseKey.get()
                                    : null
                            }
                            className="bg-gray-50 border text-xs border-gray-300 text-gray-900 rounded-lg focus:ring-mainGreen focus:border-mainGreen block w-full p-2.5"
                            required
                        />
                    </div>
                    <button
                        type="submit"
                        disabled={licenseActivationState === LICENSE_ACTIVATION_STATES.PROCESSING}
                        className={classNames(
                            'w-full text-white flex items-center justify-center focus:outline-none font-medium rounded-lg text-sm px-5 py-2.5 text-center',
                            {
                                'opacity-90': licenseActivationState === LICENSE_ACTIVATION_STATES.PROCESSING,
                                'cursor-default': licenseActivationState === LICENSE_ACTIVATION_STATES.PROCESSING,
                                'hover:opacity-80': licenseActivationState !== LICENSE_ACTIVATION_STATES.PROCESSING,
                            },
                            getButtonColorClass()
                        )}
                    >
                        {licenseActivationState === LICENSE_ACTIVATION_STATES.PROCESSING ? spinnerIcon : null}
                        {getLicenseButtonLabel()}
                    </button>
                </form>
            </div>
        </div>
    );
};
